
.App {
  text-align: center;
  font-family: Arial, sans-serif;
}

h1 {
  font-size: 32px;
  margin-bottom: 20px;
}

form {
  margin-bottom: 20px;
}

input[type="text"] {
  padding: 10px;
  width: 300px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-right: 10px;
}

button[type="submit"] {
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button[type="submit"]:hover {
  background-color: #0056b3;
}

.weather-details {
  background-color: #f7f7f7;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  text-align: left;
  margin: 0 auto;
  max-width: 400px;
}

.weather-details h2 {
  font-size: 24px;
  margin-bottom: 10px;
}

.weather-details h3 {
  font-size: 20px;
  margin: 10px 0;
}

.forecast {
  margin-top: 20px;
}

ul {
  list-style: none;
  padding: 0;
}

li {
  font-size: 16px;
  margin: 5px 0;
}




.weather-box {
  background-color: #f7f7f7;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  text-align: left;
  margin: 10px auto;
  max-width: 400px;
}

.weather-box h3 {
  font-size: 20px;
  margin: 10px 0;
}


.day-1 {
  background-color: #ffeb3b; 
}

.day-2 {
  background-color: #ff9800; 
}

.day-3 {
  background-color: #4caf50; 
}

.day-4 {
  background-color: #2196f3; 
}

.day-5 {
  background-color: #e91e63; 
}


body {
  background-color: black;
}
h1 {
  color: white;
}